/* Regis Habineza CSS Style*/
/** portfolio designed in Reactjs and HTML and CSS*/
/* Neza Regis Style*/

* {
  margin: 0;
  padding: 0;
}

/* box-sizing and font sizing */
*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;

  /* Set font size for easy rem calculations
   * default document font size = 16px, 1rem = 16px, 100% = 16px
   * (100% / 16px) * 10 = 62.5%, 1rem = 10px, 62.5% = 10px
  */
  font-size: 90.5%;
  scroll-behavior: smooth;
}

/* 1200px / 16px = 75em */
@media (max-width: 75em) {
  html {
    font-size: 60%;
  }
}
/* 980px / 16px = 61.25em */
@media (max-width: 61.25em) {
  html {
    font-size: 58%;
  }
}

/* 460px / 16px = 28.75em */
@media (max-width: 28.75em) {
  html {
    font-size: 55%;
  }
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  margin: 0px;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
  text-align: left;
  padding-bottom: 0px;
}

.welcome {
  background-color: rgba(255, 255, 255, 0.6);
  color: #222;
  position: absolute;
  bottom: 50%;
  left: 30%;
  width: 50%;
  height: 30%;
  border-radius: 8px;
  text-align: center;
}
@media (min-width: 576px) {
  .cover-text > p {
    line-height: 150px;
    font-weight: 500;
    font-size: 20px;
  }
}

/* project section region*/
.projects-section {
  text-align: center;
  padding: 6rem 2rem;
  background: var(--main-blue);
  width: 80%;
}
.projects-section-header {
  max-width: 640px;
  margin: 0 auto 2rem auto;
  border-bottom: 0.2rem solid var(--main-white);
}

@media (max-width: 28.75em) {
  .projects-section-header {
    font-size: 4rem;
  }
}
/* "Automagic" image grid using no media queries */
.projects-section {
  text-align: center;
  padding: 3rem 2rem;
  background: var(--main-blue);
  width: 100%;
}
.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  grid-template-rows: repeat(minmax(320xp, 1fr));
  grid-gap: 1.5rem;
  width: 80%;
  max-width: 1280px;
  margin: 0 auto;
}
.project-image {
  height: 100%;
  width: 100%;
  object-fit: cover;
  display: block;
}
.project-title {
  font-size: 15px;
  font-weight: bold;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  color: white;
}
.project-text {
  color: black;
  font-size: 15px;
  font-weight: bold;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  text-align: center;
}
.project-title:hover {
  color: blue;
}
@media (max-width: 30.625em) {
  .projects-section {
    padding: 4rem 1rem;
  }

  .projects-grid {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
  }
}
.container::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: -1;
  background: var(--color-darkblue);
  background-image: linear-gradient(115deg, #004e92, #ffd89b, #19547b);
  background-image: transparent;
}

.first-photo {
  background-size: cover;
  height: 400px;
  width: 100%;
  background-image: transparent;
}

#profile-address {
  text-align: center;
}

@media (min-width: 576px) {
  .container {
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
    width: 80%;
  }
  .btn-around {
    text-align: center;
  }
  figcaption {
    font-weight: 500;
  }
  article {
    display: block;
  }
}

.first-img {
  margin-bottom: 2%;
  border-radius: 50%;
}
img {
  height: 30vmin;
  vertical-align: middle;
  text-align: center;
}
h3 {
  text-align: center;
}
.about-text {
  width: 100%;
}
.profile-container {
  text-align: center;
  padding-top: 10px;
}

/*button contain Resume*/

@-webkit-keyframes pulse {
  0% {
    -webkit-transform: scale(0);
    transform: scale(0);
    opacity: 0;
  }
  33% {
    -webkit-transform: scale(1);
    transform: scale(1);
    opacity: 1;
  }
  100% {
    -webkit-transform: scale(3);
    transform: scale(3);
    opacity: 0;
  }
}

@keyframes pulse {
  0% {
    -webkit-transform: scale(0);
    transform: scale(0);
    opacity: 0;
  }
  33% {
    -webkit-transform: scale(1);
    transform: scale(1);
    opacity: 1;
  }
  100% {
    -webkit-transform: scale(3);
    transform: scale(3);
    opacity: 0;
  }
}

.button {
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  line-height: 35px;
  background: rgb(31, 27, 28);
  -webkit-box-shadow: 0 3px 2px 0 rgba(0, 0, 0, 0.1);
  box-shadow: 0 3px 2px 0 rgba(0, 0, 0, 0.1);
  border-bottom-left-radius: 15px;
  border-top-right-radius: 15px;
  border: none;
  height: 40px;
  padding: 0 30px;
  color: #fff;
  font-family: Lato, Arial, sans-serif;
  text-transform: uppercase;
  text-decoration: none;
  -webkit-transition: 0.3s, -webkit-transform 0.3s, -webkit-box-shadow 0.3s;
  transition: 0.3s, -webkit-transform 0.3s, -webkit-box-shadow 0.3s;
  transition: 0.3s, transform 0.3s, box-shadow 0.3s;
  transition: 0.3s, transform 0.3s, box-shadow 0.3s, -webkit-transform 0.3s,
    -webkit-box-shadow 0.3s;
  will-change: transform;
}

.button:hover {
  background: greenyellow;
  -webkit-box-shadow: 0 4px 17px rgba(0, 0, 0, 0.2);
  box-shadow: 0 4px 17px rgba(0, 0, 0, 0.2);
  -webkit-transform: translate3d(0, -2px, 0);
  transform: translate3d(0, -2px, 0);
}

.button:active {
  -webkit-box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.1);
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.1);
  -webkit-transform: translate3d(0, 1px, 0);
  transform: translate3d(0, 1px, 0);
}

.pulse {
  position: relative;
}

.pulse:before,
.pulse:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(255, 255, 255, 0.4);
  border-radius: 50%;
  width: 20px;
  height: 20px;
  opacity: 0;
  margin: auto;
}

.pulse:before {
  -webkit-animation: pulse 1.5s infinite linear;
  animation: pulse 1.5s infinite linear;
}

.pulse:after {
  -webkit-animation: pulse 2s 0.4s infinite linear;
  animation: pulse 2s 0.4s infinite linear;
}

.pulse:hover:before,
.pulse:hover:after {
  display: none;
}
