/*CONTACT-Cards Style Designe*/

#follow-link {
  display: flex;
  font-size: 20px;
  margin: 15px;
  text-align: center;
}

/*- Card container -*/
.card-container {
  position: relative;
  z-index: 1;
  margin: auto;
  max-width: 600px;
  height: 500px;
  perspective: 1000px;
  display: flex;
  flex-direction: row;
}

.card-picture {
  max-width: 100%;
  max-height: 100%;
  border-radius: 8px;
}

/*- Card body -*/
.card-body {
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
  transition: all 0.7s linear;
  background-image: linear-gradient(to bottom, black, orange);
}

/*- Flipping the card -*/
.card-container:hover .card-body {
  transform: rotateY(180deg);
}

.card-container:hover > .card-body > .side-front {
  opacity: 0;
  visibility: hidden;
  transition: opacity 1s ease-in, visibility 0.75s linear;
}

/*- Card sides -*/
.card-side {
  position: absolute;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100%;
  color: #212121;
  background-image: linear-gradient(
    115deg,
    rgba(58, 58, 158, 0.8),
    rgba(136, 136, 206, 0.7)
  );
  backface-visibility: hidden;
  box-shadow: 0 10px 35px rgba(50, 50, 93, 0.1), 0 2px 15px rgba(0, 0, 0, 0.07);
}

/*- Front side -*/
.side-front [class^="col-xs"]:first-of-type {
  padding-left: 0;
}

.side-front-content {
  padding-top: 0px;
  padding-right: 0px;
  padding-bottom: 0px;
}

/*- Back side -*/
.side-back {
  z-index: 2;
  padding: 32px;
  text-align: center;
  transform: rotateY(180deg);
}
h5,
h4 {
  font-weight: bold;
}

input[type="button"] {
  border: none;
  width: 80px;
  background: grey;
  color: #000;
  line-height: 20px;
  font-size: 16px;
  padding: 10px 0px;
  border-radius: 10px;
  cursor: pointer;
  align-items: justify;
}
input[type="button"]:hover {
  color: #fff;
  background-color: black;
}
textarea[type="text"] {
  width: 165px;
  height: 50px;
}
